// src/components/Sidebar.tsx
"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import {
  Trophy,
  LayoutDashboard,
  Settings,
  ShieldCheck,
  Shield,
} from "lucide-react";
import Image from "next/image";
import clsx from "clsx";

type Props = {
  onNavigate?: () => void;
  isAdmin?: boolean;
  /** Quando true, mostra s√≥ √≠cones (mesma <aside>, sem rail extra) */
  collapsed?: boolean;
};

type Item = { href: string; label: string; icon: React.ComponentType<any> };

export default function Sidebar({
  onNavigate,
  isAdmin,
  collapsed = false,
}: Props) {
  const pathname = usePathname();

  const items: Item[] = [
    { href: "/me", label: "Meu Painel", icon: LayoutDashboard },
    { href: "/ranking", label: "Ranking", icon: Trophy },
    { href: "/config", label: "Configura√ß√µes", icon: Settings },
    { href: "/me/security", label: "Seguran√ßa", icon: Shield },
  ];

  if (isAdmin) {
    items.push({ href: "/admin", label: "Administra√ß√£o", icon: ShieldCheck });
  }

  return (
    <aside
      className={clsx(
        // ‚ùó UMA √öNICA <aside> cuja largura muda ‚Äî sem sticky/fixed/second rail
        "border-r border-white/10 bg-[var(--surface)]/70 backdrop-blur flex flex-col",
        collapsed ? "w-[4rem]" : "w-[18rem]"
      )}
    >
      {/* Header */}
      <div
        className={clsx(
          "h-14 border-b border-white/10 px-4 flex items-center gap-2",
          collapsed && "justify-center px-0"
        )}
      >
        <Link
          href="/"
          className={clsx(
            "flex items-center gap-2 transition-transform hover:scale-[1.02]",
            collapsed && "justify-center"
          )}
          onClick={onNavigate}
          aria-label="Ir para a p√°gina inicial"
        >
          <Image
            src="/logo.png"
            alt="EstudoRank"
            width={collapsed ? 20 : 26}
            height={collapsed ? 20 : 26}
            className="rounded-sm drop-shadow-[0_0_6px_rgba(163,230,53,0.30)]"
            priority
          />
          {!collapsed && (
            <span className="font-semibold tracking-tight">EstudoRank</span>
          )}
        </Link>
      </div>

      {/* Navega√ß√£o */}
      <nav className={clsx("flex-1 p-3 space-y-2", collapsed && "p-2")}>
        {items.map((item) => {
          const active =
            pathname === item.href || pathname.startsWith(`${item.href}/`);
          return (
            <NavItem
              key={item.href}
              item={item}
              active={active}
              collapsed={collapsed}
              onNavigate={onNavigate}
            />
          );
        })}
      </nav>

      {/* Footer */}
      {!collapsed ? (
        <div className="px-3 py-3 text-xs opacity-60 border-t border-white/10">
          ¬© {new Date().getFullYear()} EstudoRank
        </div>
      ) : (
        <div className="py-3 border-t border-white/10 text-center text-[10px] opacity-50 select-none">
          ¬© {new Date().getFullYear()}
        </div>
      )}
    </aside>
  );
}

/* --------------------------- Subcomponentes --------------------------- */

function NavItem({
  item,
  active,
  collapsed,
  onNavigate,
}: {
  item: Item;
  active: boolean;
  collapsed: boolean;
  onNavigate?: () => void;
}) {
  const Icon = item.icon;

  return (
    <Link
      href={item.href}
      onClick={onNavigate}
      aria-current={active ? "page" : undefined}
      className={clsx(
        "group relative flex items-center gap-3 rounded-xl border transition-all duration-200 outline-none focus-visible:ring-2 focus-visible:ring-accent/40",
        collapsed ? "justify-center py-2" : "px-3 py-2",
        active
          ? "bg-white/[0.06] border-white/20 shadow-[0_0_0_1px_rgba(255,255,255,0.06)] text-lime-200"
          : "bg-white/[0.04] hover:bg-white/[0.08] border-white/10 text-white/85 hover:text-white"
      )}
    >
      <span className="inline-flex h-8 w-8 shrink-0 items-center justify-center rounded-lg border border-white/10 bg-white/[0.04] group-hover:bg-white/[0.08]">
        <Icon size={16} className={active ? "text-accent" : "opacity-80"} />
      </span>

      {!collapsed && <span className="text-sm">{item.label}</span>}

      {/* Tooltip quando colapsado */}
      {collapsed && (
        <span
          className={clsx(
            "pointer-events-none absolute left-full ml-3 top-1/2 -translate-y-1/2",
            "rounded-md bg-black/80 text-white text-xs px-2 py-1",
            "opacity-0 -translate-x-1 group-hover:opacity-100 group-hover:translate-x-0",
            "transition-all whitespace-nowrap z-50"
          )}
          role="tooltip"
        >
          {item.label}
        </span>
      )}
    </Link>
  );
}
